.billing__container {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(14rem, 1fr));
  grid-gap: 2rem;
  margin: 2rem;
}

.count {
  font-size: 4rem;
  font-weight: 700;
  color: $color-blue-grey;
  padding-left: 1rem;
  border-left: 1px solid #adb2b5;
}

.count__top,
.count__bottom {
  font-size: 1.3rem;
  font-weight: 400;
  color: $color-blue-grey;
  padding-left: 1rem;
  line-height: 1.5;
}

.span__count--positive {
  color: $color-positive;
}

.span__count--negative {
  color: $color-negative;
}
